import React from "react";
import { Link } from "react-router-dom";
import { FaAngleDown } from "react-icons/fa";

const Nav = () => {
   return (
      <nav className="mainmenu-nav">
         <ul className="mainmenu">
            <li>
               <Link to={process.env.PUBLIC_URL + "/about-us"}>About Us</Link>
            </li>
            <li>
               <Link to={process.env.PUBLIC_URL + "/service-one"}>
                  Services
               </Link>
            </li>
            <li className="menu-item-has-children">
               <Link to={process.env.PUBLIC_URL + "/project-grid-one"}>
                  Different Disorders <FaAngleDown />
               </Link>
               <ul className="axil-submenu">
                  <li>
                     <Link
                        to={
                           process.env.PUBLIC_URL +
                           "/service-details/learning-disabilities"
                        }
                     >
                        Learning Disabilities
                     </Link>
                  </li>
                  <li>
                     <Link
                        to={
                           process.env.PUBLIC_URL +
                           "/service-details/attention-deficit-hyperactivity-disorder-adhd"
                        }
                     >
                        ADHD
                     </Link>
                  </li>
                  <li>
                     <Link
                        to={
                           process.env.PUBLIC_URL +
                           "/service-details/behaviour-disorders-emotional-disturbances"
                        }
                     >
                        Behaviour Disorders
                     </Link>
                  </li>
                  <li>
                     <Link
                        to={
                           process.env.PUBLIC_URL +
                           "/service-details/communication-disorders"
                        }
                     >
                        Communication Disorders
                     </Link>
                  </li>
                  <li>
                     <Link
                        to={process.env.PUBLIC_URL + "/service-details/autism"}
                     >
                        Autism
                     </Link>
                  </li>
               </ul>
            </li>
            <li>
               <Link to={process.env.PUBLIC_URL + "/our-clients"}>Videos</Link>
            </li>
            <li>
               <Link to={process.env.PUBLIC_URL + "/contact"}>Contact</Link>
            </li>
         </ul>
      </nav>
   );
};

export default Nav;
